import {addMethod} from "@xbs/muon";

export function init(){
	addMethod("IMAGE", function(url){
		return `<img class="webix_ssheet_cimage" src="${webix.template.escape(url)}">`;
	});

	addMethod("SPARKLINE", function(arr, type, color1, color2){
		var i, config = { type: type, color: color1, negativeColor:color2  },
			width =  type == "pie"? 60: 150;
		for(i =0 ; i < arr.length; i++)
			arr[i] = arr[i]||0;
		return webix.Sparklines.getTemplate(config)(arr, { width:width, height: 35 });
	});

	addMethod("HYPERLINK", function(url, text){
		text = text || url;
		return `<a target="blank" href="${webix.template.escape(url)}">${webix.template.escape(text)}</a>`;
	});
}

export function getJsDateFromExcel(excelDate) {
	const date = new Date(Math.round((excelDate - 25569) * 86400 * 1000));
	return new Date(date.getTime() + date.getTimezoneOffset() * 60000);
}

export function getExcelDateFromJs(jsDate) {
	const returnDateTime = 25569 + ((jsDate.getTime() - (jsDate.getTimezoneOffset() * 60000)) / (86400 * 1000));
	return returnDateTime.toString();
}